.spin-container {
    text-align: center;
    min-width : 300px;
    position: relative;
    top: 20%;
}

.spin {
    height            : 200px;
    width             : 200px;
    border            : 3px solid #0d4b9f;
    margin            : 0 auto;
    border-radius     : 50%;
    border-left-color : transparent;
    border-right-color: transparent;
    animation         : rotate 2s cubic-bezier(0.26, 1.36, 0.74, -0.29) infinite;
}

#loader2 {
    border            : 3px solid #e0edff;
    width             : 220px;
    height            : 220px;
    position          : relative;
    top               : -216px;
    border-left-color : transparent;
    border-right-color: transparent;
    animation         : rotate2 2s cubic-bezier(0.26, 1.36, 0.74, -0.29) infinite;
}

#loader3 {
    border            : 3px solid #94b6e5;
    width             : 240px;
    height            : 240px;
    position          : relative;
    top               : -452px;
    border-left-color : transparent;
    border-right-color: transparent;
    animation         : rotate 2s cubic-bezier(0.26, 1.36, 0.74, -0.29) infinite;
}

#loader4 {
    border            : 3px solid #94b6e5;
    width             : 260px;
    height            : 260px;
    position          : relative;
    top               : -708px;
    border-left-color : transparent;
    border-right-color: transparent;
    animation         : rotate2 2s cubic-bezier(0.26, 1.36, 0.74, -0.29) infinite;
}

@keyframes rotate {
    0% {
        transform: rotateZ(-360deg);
    }

    100% {
        transform: rotateZ(0deg);
    }
}

@keyframes rotate2 {
    0% {
        transform: rotateZ(360deg);
    }

    100% {
        transform: rotateZ(0deg);
    }
}

#text {
    position   : relative;
    top        : -857px;
    font-weight: bold;
}